@import url(https://fonts.googleapis.com/css?family=Poppins:300&display=swap);
/* Navbar */
#nav{
    z-index: 1;
    background-color: #fff;
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
}
#nav .navbar-brand{
    font-weight: 800;
    padding: 20px 0px;
    color: #000;
}
#nav .toggler{
    color: black;
}
#nav .nav-item{
    margin: 10px 30px;
}
#nav .nav-item .nav-link{
    color: #fff;
}
#nav .nav-item .active{
    border-bottom: 2px solid #F8A22F;
}
#showcase{
    height: 80vh;
}
#showcase .showcase-content{
    padding-right: 100px;
}
#showcase .showcase-content .showcase-heading{
    text-align: left;
    font-family: Poppins , sans-serif;
    font-size: 50px;
    letter-spacing: 0;
    color: #242424;
}

#showcase .showcase-content .showcase-lead{
    text-align: left;
    font: Regular 26px/41px Poppins;
    letter-spacing: 0;
    color: #242424;
    opacity: 0.5;
}
.showcase-img{
    position: absolute;
    top:0;
    right:0;
    background: url(/static/media/img1.61fbdb11.png) no-repeat center center/cover;
    opacity: .84;
    height: 100vh;
    width:100%;
    -webkit-clip-path: circle(55% at 83% 13%);
            clip-path: circle(55% at 83% 13%);
}

@media screen and (max-width: 568px){
    .showcase-img{
        display: none;
    }
}
/* The Whole Card */
.ApplicationCard{
    height: 600px;
    min-width: 300px;
    width: auto;
    position: relative;
}

/* Card Title */
.ApplicationCard .card-title{
    margin:30px 0;
    font-size: 26px;
    text-align: center;
}

/* Card Item */
.ApplicationCard .card-item{
    padding:10px 20px;
    margin: 0 0px;
    margin-bottom: 15px;
    height: 100px;
    max-width: 100%;
}

/* Card Item Hover */
.ApplicationCard .card-item:hover{
    background-color: #F8A22F;
    cursor: pointer;
}

/* Card Image */
.ApplicationCard .card-item .card-img{
    padding: 10px 0;
    border: none;
    border-radius: 0;
    max-width: 95px;
    min-height: 100px;
}

/* Card Item Name */
.ApplicationCard .card-item .item-name{
    padding: 0 20px;
    margin-right: auto;
    font-weight: lighter;
}

/* Card Item Prize */
.ApplicationCard .card-item .item-prize{
    text-align: right;
    font-weight: bolder;
}



.btnGroup{
    padding: 20px 0;
    text-align: center;
}
#availableSec{
    margin-top: 100px;
    height: 100vh;
    overflow: auto;
    background:
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(9, 12, 56, 0.9)), 
      to(rgba(9, 12, 56, 0.9))
    ),
    url(/static/media/img1.61fbdb11.png) 0% 0% no-repeat padding-box;
    background:
    linear-gradient(
      rgba(9, 12, 56, 0.9), 
      rgba(9, 12, 56, 0.9)
    ),
    url(/static/media/img1.61fbdb11.png) 0% 0% no-repeat padding-box;
}
#availableSecContainer{
  padding: 50px 0;
}
.ApplicationCard .total{
  margin-top:10px;
  position: absolute;
  bottom: 0px;
  height: 50px;
  border-radius: inherit;
  text-align: justify;
  padding: 25px 10px;
  background-color: #F8A22F;
  width: 100%;
  font-weight: bolder;
}
.ApplicationCard .noItems{
  text-align: center;
  padding: 10px;
  height: 100%;
  width: 100%;
  
}
.ApplicationCard .noItems h2{
  font-size: 26px;
  opacity: 0.5;
}
.ApplicationCard .noItems p{
  font-size: 15px;
  opacity: 0.4;
}
*{
    font-family: 'Poppins', sans-serif;
}
/* Utlity Classes */
.primary-color{
    color: #F8A22F;
}

.primary-btn{
    box-sizing: border-box;
    background: #F8A22F;
    border-radius: 32px;
    color: #fff;
    border: none;
    padding: 17px 55px;
}

.text-center{
    text-align: center;
}

.disable{
    background: rgba(248, 161, 47, 0.171);
    cursor: not-allowed;
    opacity: .4;
}

.item-active{
    background-color: #F8A22F;
}
